const directory = 'LANGUAGE_';

export const ActionTypes = {
    LOAD_LANGUAGE: `${ directory }LOAD_LANGUAGE`,
    SELECT_LANGUAGE: `${ directory }SELECT_LANGUAGE`
};

export function loadLanguage(language) {
    return { type: ActionTypes.LOAD_LANGUAGE, payload: language }
}

export function selectLanguage(language) {
    return { type: ActionTypes.SELECT_LANGUAGE, payload: language }
}
