const directory = 'ACCOUNT_';

export const ActionTypes = {
    RESET_FORM: `${ directory }RESET_FORM`,
    EMAIL_CHANGE: `${ directory }EMAIL_CHANGE`,
    PASSWORD_CHANGE: `${ directory }PASSWORD_CHANGE`,
    SAVE_EMAIL: `${ directory }SAVE_EMAIL`,
    SAVE_PASSWORD: `${ directory }SAVE_PASSWORD`,
    OPEN_DELETE_ACCOUNT_DIALOG: `${ directory }OPEN_DELETE_ACCOUNT_DIALOG`,
    CLOSE_DELETE_ACCOUNT_DIALOG: `${ directory }CLOSE_DELETE_ACCOUNT_DIALOG`,
};

export function resetForm() {
    return { type: ActionTypes.RESET_FORM }
}

export function emailChange(email) {
    return { type: ActionTypes.EMAIL_CHANGE, payload: email }
}

export function passwordChange(password) {
    return { type: ActionTypes.PASSWORD_CHANGE, payload: password }
}

export function saveEmail(error) {
    return { type: ActionTypes.SAVE_EMAIL, payload: error }
}

export function savePassword(error) {
    return { type: ActionTypes.SAVE_PASSWORD, payload: error }
}

export function openDeleteAccountDialog() {
    return { type: ActionTypes.OPEN_DELETE_ACCOUNT_DIALOG }
}

export function closeDeleteAccountDialog() {
    return { type: ActionTypes.CLOSE_DELETE_ACCOUNT_DIALOG }
}
