// Action Types
import { ActionTypes } from '../actions/CreateAccount.Actions';

const init = {
    error: {
        isError: false,
        errorMessage: ''
    },
    email: {
        touched: false,
        valid: false,
        value: ''
    },
    password: {
        touched: false,
        valid: false,
        value: ''
    }
};

const emailPattern = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

const createAccount = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.INIT:
        return Object.assign({}, {
            error: {
                isError: false,
                errorMessage: ''
            },
            email: {
                touched: false,
                valid: false,
                value: ''
            },
            password: {
                touched: false,
                valid: false,
                value: ''
            }
        });

        case ActionTypes.EMAIL_CHANGE:
            return Object.assign({}, state, {
                email: {
                    touched: true,
                    valid: emailPattern.test(action.payload) && action.payload,
                    value: action.payload
                },
                error: {
                    isError: false,
                    errorMessage: ''
                }
            });

            case ActionTypes.PASSWORD_CHANGE:
            return Object.assign({}, state, {
                password: {
                    touched: true,
                    valid: action.payload.length,
                    value: action.payload
                },
                error: {
                    isError: false,
                    errorMessage: ''
                }
            });

            case ActionTypes.CREATE_ACCOUNT:
            return Object.assign({}, state, {
                error: {
                    isError: action.payload ? true : false,
                    errorMessage: action.payload ? action.payload : ''
                }
            });

        default:
        return state
    }
}

export default createAccount;
