// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth, firestore } from 'firebase/app';
// Material
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Language from '@material-ui/icons/Language';
// Actions
import { loadLanguage, selectLanguage } from '../actions/Language.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';

const styles = {
    listItem: {
        borderBottom: '1px solid #ddd'
    },
    listItemText: {
        textTransform: 'capitalize',
        cursor: 'pointer'
    },
    isActive: {
        textTransform: 'capitalize',
        background: '#ccc',
        cursor: 'default'
    }
};

class LanguageListContainer extends Component {
    componentWillMount() {
        this.props.loadLanguage();
        this.props.updateTitle();
    }

    render() {
        return (
            <List component="nav">
                { this.props.languages.map(language =>
                    <ListItem
                        key={ language.value }
                        onClick={ () => this.props.selectLanguage(language) }
                        style={ this.props.languageSelected === language.value ? styles.isActive : styles.listItem }
                        button
                    >
                        <ListItemIcon><Language /></ListItemIcon>
                        <ListItemText style={ styles.listItemText } primary={ language.lang } />
                    </ListItem>
                ) }
            </List>
        )
    }
}

const mapStateToProps = state => {
    return {
        languages: state.language.languages,
        languageSelected: state.language.languageSelected
    }
}

function mapDispatchToProps(dispatch) {
    return {
        loadLanguage: () => {
            auth().onAuthStateChanged(userData => {
                if (userData) {
                    firestore().collection('users').doc(userData.uid).get()
                    .then(user => dispatch(loadLanguage((user.data() && user.data().language) ? user.data().language : 'en')));
                }
            });
        },
        selectLanguage: (language) => {
            firestore().collection('users').doc(auth().currentUser.uid).update({ language: language.value })
            .then(_ => dispatch(selectLanguage(language.value)));
        },
        updateTitle: () => { dispatch(updateTitle('Language')) }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LanguageListContainer);
