const directory = 'CARDS_';

export const ActionTypes = {
    LOAD_CARDS: `${ directory }LOAD_CARDS`,
    CLEAR_CARDS: `${ directory }CLEAR_CARDS`,
    PREVIOUS: `${ directory }PREVIOUS`,
    NEXT: `${ directory }NEXT`,
    DELETE_CARD: `${ directory }DELETE_CARD`,
    CONFIRM_DELETE_CARD: `${ directory }CONFIRM_DELETE_CARD`,
    CLOSE_DELETE_CARD_DIALOG: `${ directory }CLOSE_DELETE_CARD_DIALOG`
};

export function loadCards({ cards, folderName }) {
    return { type: ActionTypes.LOAD_CARDS, payload: { cards, folderName } }
}

export function clearCards() {
    return { type: ActionTypes.CLEAR_CARDS }
}

export function previous() {
    return { type: ActionTypes.PREVIOUS }
}

export function next() {
    return { type: ActionTypes.NEXT }
}

export function deleteCard(card) {
    return { type: ActionTypes.DELETE_CARD, payload: card }
}

export function confirmDeleteCard(activeIndex) {
    return { type: ActionTypes.CONFIRM_DELETE_CARD, payload: activeIndex }
}

export function closeDeleteCardDialog() {
    return { type: ActionTypes.CLOSE_DELETE_CARD_DIALOG }
}
