// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth, firestore } from 'firebase/app';
// Material
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';
// Actions
import { closeDeleteAccountDialog, resetForm } from '../actions/Account.Actions';

class DeleteAccountDialogContainer extends Component {
    render() {
        return (
            <Dialog open={ this.props.deleteAccountDialogOpen }>
                <DialogContent>
                    <DialogContentText>Are you sure you want to delete your account?</DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={ this.props.closeDeleteAccountDialog } color="secondary">Cancel</Button>
                    <Button onClick={ this.props.deleteAccount } color="primary">Confirm</Button>
                </DialogActions>
            </Dialog>
        )
    }
};

const mapStateToProps = state => {
    return {
        deleteAccountDialogOpen: state.account.deleteAccountDialogOpen
    }
}

function mapDispatchToProps(dispatch) {
    return {
        closeDeleteAccountDialog: () => { dispatch(closeDeleteAccountDialog()) },
        deleteAccount: () => {
            firestore().collection('users').doc(auth().currentUser.uid).delete()
            .then(_ => auth().signOut().then(_ => dispatch(resetForm())));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DeleteAccountDialogContainer);
