const directory = 'RESET_PASSWORD_';

export const ActionTypes = {
    OPEN_RESET_PASSWORD_DIALOG: `${ directory }OPEN_RESET_PASSWORD_DIALOG`,
    CLOSE_RESET_PASSWORD_DIALOG: `${ directory }CLOSE_RESET_PASSWORD_DIALOG`,
    RESET_PASSWORD_EMAIL_CHANGE: `${ directory }RESET_PASSWORD_EMAIL_CHANGE`,
    RESET_PASSWORD: `${ directory }RESET_PASSWORD`
};

export function openResetPasswordDialog() {
    return { type: ActionTypes.OPEN_RESET_PASSWORD_DIALOG }
}

export function closeResetPasswordDialog() {
    return { type: ActionTypes.CLOSE_RESET_PASSWORD_DIALOG }
}

export function resetPasswordEmailChange(email) {
    return { type: ActionTypes.RESET_PASSWORD_EMAIL_CHANGE, payload: email }
}

export function resetPassword({ resetPasswordMessage, resetPasswordError }) {
    return { type: ActionTypes.RESET_PASSWORD, payload: { resetPasswordMessage, resetPasswordError } }
}
