// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth } from 'firebase/app';
// Material
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Email from '@material-ui/icons/Email';
// Actions
import { closeResetPasswordDialog, resetPasswordEmailChange, resetPassword } from '../actions/ResetPassword.Action';

const styles = {
    confirmationMessage: {
        color: 'green',
        border: '1px solid green',
        fontSize: '12px',
        margin: '24px 16px 0px',
        width: '220px',
        textAlign: 'center'
    },
    errorMessage: {
        color: 'red',
        border: '1px solid red',
        fontSize: '12px',
        margin: '24px 16px 0px',
        maxWidth: '220px',
        textAlign: 'center'
    }
};

class ResetPasswordContainer extends Component {
    render() {
        return (
            <Dialog open={ this.props.resetPasswordDialogOpen }>
                <DialogTitle>Reset Password</DialogTitle>
                <DialogContent>
                    <Grid alignItems="flex-end" spacing={ 8 } container>
                        <Grid item><Email color="action" /></Grid>
                        <Grid item>
                            <TextField
                                onChange={ this.props.resetPasswordEmailChange }
                                value={ this.props.resetPasswordEmail.value }
                                label="Email"
                                required
                            />
                        </Grid>
                    </Grid>
                    {
                        this.props.resetPasswordMessage ?
                            <Grid alignItems="flex-end" spacing={ 8 } container>
                                <Grid style={ this.props.resetPasswordError ? styles.errorMessage : styles.confirmationMessage } item>
                                    <div>{ this.props.resetPasswordMessage }</div>
                                </Grid>
                            </Grid>
                        : null
                    }                    
                </DialogContent>
                <DialogActions>
                    <Button onClick={ this.props.closeResetPasswordDialog } color="secondary">Close</Button>
                    <Button
                        onClick={ () => this.props.resetPassword(this.props.resetPasswordEmail.value) }
                        disabled={ !this.props.resetPasswordEmail.valid }
                        color="primary"
                    >
                        Send Reset Link
                    </Button>
                </DialogActions>
            </Dialog>
        )
    }
};

const mapStateToProps = state => {
    return {
        resetPasswordDialogOpen: state.resetPassword.resetPasswordDialogOpen,
        resetPasswordEmail: state.resetPassword.resetPasswordEmail,
        resetPasswordEmailChange: state.resetPassword.resetPasswordEmailChange,
        resetPasswordMessage: state.resetPassword.resetPasswordMessage,
        resetPasswordError: state.resetPassword.resetPasswordError
    }
}

function mapDispatchToProps(dispatch) {
    return {
        closeResetPasswordDialog: () => { dispatch(closeResetPasswordDialog()) },
        resetPasswordEmailChange: (e) => { dispatch(resetPasswordEmailChange(e.target.value)) },
        resetPassword: (email) => {
            auth().sendPasswordResetEmail(email)
            .then(_ => dispatch(resetPassword({
                resetPasswordMessage: 'Reset email sent succesfully.',
                resetPasswordError: false
            })))
            .catch(error => dispatch(resetPassword({
                resetPasswordMessage: error.message,
                resetPasswordError: true
            })))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ResetPasswordContainer);
