// Action Types
import { ActionTypes } from '../actions/ResetPassword.Action';

const init = {
    resetPasswordMessage: '',
    resetPasswordError: false,
    resetPasswordEmail: {
        touched: false,
        valid: false,
        value: ''
    },
    resetPasswordDialogOpen: false
};

const emailPattern = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

const resetPassword = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.OPEN_RESET_PASSWORD_DIALOG:
        return Object.assign({}, state, {
            resetPasswordDialogOpen: true
        });

        case ActionTypes.CLOSE_RESET_PASSWORD_DIALOG:
        return Object.assign({}, {
            resetPasswordEmail: {
                touched: false,
                valid: true,
                value: ''
            },
            resetPasswordMessage: '',
            resetPasswordError: false,
            resetPasswordDialogOpen: false
        });

        case ActionTypes.RESET_PASSWORD_EMAIL_CHANGE:
        return Object.assign({}, state, {
            resetPasswordEmail: {
                touched: true,
                valid: emailPattern.test(action.payload) && action.payload.length,
                value: action.payload
            },
            resetPasswordMessage: '',
            resetPasswordError: false,
            resetPasswordDialogOpen: state.resetPasswordDialogOpen
        });

        case ActionTypes.RESET_PASSWORD:
        return Object.assign({}, state, {
            resetPasswordEmail: {
                touched: false,
                valid: true,
                value: action.payload.resetPasswordError ? state.resetPasswordEmail.value : ''
            },
            resetPasswordMessage: action.payload.resetPasswordMessage,
            resetPasswordError: action.payload.resetPasswordError,
        });

        default:
        return state
    }
}

export default resetPassword;