// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth } from 'firebase/app';
// Material
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Save from '@material-ui/icons/Save';
// Actions
import { resetForm, emailChange, passwordChange, saveEmail, savePassword, openDeleteAccountDialog } from '../actions/Account.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';

const styles = {
    card: {
        width: '300px',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px'
    },
    header: {
        padding: '16px',
        margin: '0px',
        borderBottom: '1px solid #ddd'
    },
    cardContent: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-around',
        height: '160px'
    },
    gridContainer: {
        placeContent: 'space-evenly',
    },
    deleteAccountBtn: {
        borderTop: '1px solid #ddd',
        height: '64px',
        width: '100%'
    },
    errorMessage: {
        color: 'red',
        border: '1px solid red',
        fontSize: '12px',
        margin: '24px 16px 0px',
        textAlign: 'center'
    }
};

class AccountFormContainer extends Component {
    componentWillMount() {
        this.props.resetForm();
        this.props.updateTitle();
    }
    componentWillUnmount() {
        this.props.resetForm();
    }

    render() {
        return (
            <div>
                <Card style={ styles.card }>
                    <h4 style={ styles.header }>Update Account</h4>
                    <CardContent style={ styles.cardContent }>
                        <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                            <Grid item>
                                <TextField
                                    onChange={ this.props.emailChange }
                                    value={ this.props.email.value }
                                    error={ this.props.email.touched && !this.props.email.valid }
                                    label="Update Email"
                                    type="email"
                                    required
                                />
                            </Grid>
                            <Grid item>
                                <IconButton
                                    onClick={ () => this.props.saveEmail(this.props.email.value) }
                                    disabled={ !this.props.email.valid }
                                    color="primary"
                                >
                                    <Save />
                                </IconButton>
                            </Grid>
                        </Grid>
                        <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                            <Grid item>
                                <TextField
                                    onChange={ this.props.passwordChange }
                                    value={ this.props.password.value }
                                    error={ this.props.password.touched && !this.props.password.valid }
                                    label="Update Password"
                                    type="password"
                                    required
                                />
                            </Grid>
                            <Grid item>
                                <IconButton
                                    onClick={ () => this.props.savePassword(this.props.password.value) }
                                    disabled={ !this.props.password.valid }
                                    color="primary"
                                >
                                    <Save />
                                </IconButton>
                            </Grid>
                        </Grid>
                        {
                            this.props.error.isError ?
                                <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                                    <Grid style={ styles.errorMessage } item>
                                        <div>{ this.props.error.errorMessage }</div>
                                    </Grid>
                                </Grid>
                            : null
                        }
                    </CardContent>
                    <Button onClick={ this.props.openDeleteAccountDialog } style={ styles.deleteAccountBtn }>Delete Account</Button>
                </Card>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        email: state.account.email,
        password: state.account.password,
        error: state.account.error
    }
}

function mapDispatchToProps(dispatch) {
    return {
        resetForm: () => { dispatch(resetForm()) },
        emailChange: (e) => { dispatch(emailChange(e.target.value)) },
        passwordChange: (e) => { dispatch(passwordChange(e.target.value)) },
        saveEmail: (email) => {
            auth().currentUser.updateEmail(email)
            .then(_ => dispatch(saveEmail(null)))
            .catch(error => dispatch(saveEmail(error.message)));
        },
        savePassword: (password) => {
            auth().currentUser.updatePassword(password)
            .then(_ => dispatch(savePassword(null)))
            .catch(error => dispatch(savePassword(error.message)));
        },
        openDeleteAccountDialog: () => { dispatch(openDeleteAccountDialog()) },
        updateTitle: () => { dispatch(updateTitle('Account')) },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AccountFormContainer);
