// Action Types
import { ActionTypes } from '../actions/SearchForm.Actions';

const init = {
    articles: [],
    language: ''
};

const searchForm = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_LANGUAGE:
        return Object.assign({}, state, { language: action.payload });

        case ActionTypes.SEARCH_TITLE:
        return Object.assign({}, state, { articles: action.payload });

        case ActionTypes.INIT:
        return Object.assign({}, { articles: [], language: action.language });

        default:
        return state
    }
}

export default searchForm;