// Redux
import { combineReducers } from 'redux';
// Reducers
import login from './login/reducers';
import createAccount from './create-account/reducers';
import app from './app/reducers';
import sideNav from './sideNav/reducers';
import searchForm from './search/reducers';
import language from './language/reducers';
import article from './article/reducers';
import folders from './folders/reducers';
import cards from './cards/reducers';
import account from './account/reducers';

const allReducers = Object.assign({}, login, createAccount, app, sideNav, searchForm, language, article, folders, cards, account);

export default combineReducers(
    allReducers
);
