const directory = 'SIDENAV_';

export const ActionTypes = {
    TOGGLE_SIDENAV: `${ directory }TOGGLE_SIDENAV`,
    CLOSE_SIDENAV: `${ directory }CLOSE_SIDENAV`,
    UPDATE_TITLE: `${ directory }UPDATE_TITLE`
};

export function toggleSideNav() {
    return { type: ActionTypes.TOGGLE_SIDENAV }
}

export function closeSideNav() {
    return { type: ActionTypes.CLOSE_SIDENAV }
}

export function updateTitle(title) {
    return { type: ActionTypes.UPDATE_TITLE, payload: title }
}