// React
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth, firestore } from 'firebase/app';
// Material
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Badge from '@material-ui/core/Badge';
import Folder from '@material-ui/icons/Folder';
// Actions
import { loadFolders } from '../actions/Folders.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';


const styles = {
    listSubheader: {
        background: '#eee',
        borderBottom: '1px solid #ddd',
        top: '0px'
    },
    listItem: {
        borderBottom: '1px solid #ddd',
        paddingTop: '16px',
        paddingBottom: '8px'
    },
    listItemText: {
        textTransform: 'capitalize'
    }
};

class FoldersListContainer extends Component {
    componentWillMount() {
        this.props.loadFolders();
        this.props.updateTitle();
    }

    render() {
        return (
            <List style={ styles.list } component="nav">
                <ListSubheader style={ styles.listSubheader }>Folders</ListSubheader>
                { this.props.folders.map(folder =>
                    <ListItem
                        key={ folder.id }
                        component={ Link } to={ `/app/cards/${ folder.id }` }
                        style={ styles.listItem }
                        button
                    >
                        <ListItemIcon>
                            <Badge badgeContent={ folder.cardsLength || 0 } color="primary">
                                <Folder />
                            </Badge>
                        </ListItemIcon>
                        <ListItemText style={ styles.listItemText } primary={ folder.folderName } />
                    </ListItem>
                ) }
            </List>
        )
    }
}

const mapStateToProps = state => {
    return {
        folders: state.folders.folders
    }
}

function mapDispatchToProps(dispatch) {
    return {
        loadFolders: () => {
            auth().onAuthStateChanged(userData => {
                if (userData) {
                    firestore().collection('folders').where('userUid', '==', userData.uid).onSnapshot(folders => {
                        const foldersSorted = folders.docs.map(folder => folder.data()).sort((a, b) => {
                            if (a.folderName.toLowerCase() < b.folderName.toLowerCase())
                                return -1;
                            if (a.folderName.toLowerCase() > b.folderName.toLowerCase())
                                return 1;
                            return 0;
                        });
                        dispatch(loadFolders(foldersSorted));
                    });
                } else {
                    dispatch(loadFolders([]));
                }
            });
        },
        updateTitle: () => { dispatch(updateTitle('Folders')) }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FoldersListContainer);
