// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Router
import { Link } from 'react-router-dom';
// Firebase
import { auth } from 'firebase/app';
// Material
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Email from '@material-ui/icons/Email';
import VpnKey from '@material-ui/icons/VpnKey';
// Actions
import { emailChange, init, passwordChange, login } from '../actions/LoginForm.Actions';

const styles = {
    card: {
        width: '300px',
        height: 'auto',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-evenly',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px'
    },
    cardHeader: {
        borderBottom: '1px solid #ddd'
    },
    cardContent: {
        height: '100%',
        display: 'flex',
        justifyContent: 'space-evenly',
        flexDirection: 'column'
    },
    gridContainer: {
        justifyContent: 'space-evenly',
        marginTop: '8px'
    },
    cardActions: {
        flexDirection: 'row-reverse',
        paddingBottom: '24px',
    },
    createAccountBtn: {
        borderTop: '1px solid #ddd',
        height: '64px',
        width: '100%'
    },
    errorMessage: {
        color: 'red',
        border: '1px solid red',
        fontSize: '12px',
        margin: '24px 16px 0px',
        textAlign: 'center'
    }
};

class LoginFormContainer extends Component {
    componentWillMount() {
        this.props.init();
    }

    render() {
        return (
            <Card style={ styles.card }>
                <CardHeader title="Login" style={ styles.cardHeader }></CardHeader>
                <CardContent style={ styles.cardContent }>
                    <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                        <Grid item><Email color="action" /></Grid>
                        <Grid item>
                            <TextField
                                onChange={ this.props.emailChange }
                                value={ this.props.email.value }
                                error={ this.props.email.touched && !this.props.email.valid }
                                label="Email"
                                type="email"
                                required
                            />
                        </Grid>
                    </Grid>
                    <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                        <Grid item><VpnKey color="action" /></Grid>
                        <Grid item>
                            <TextField
                                onChange={ this.props.passwordChange }
                                value={ this.props.password.value }
                                error={ this.props.password.touched && !this.props.password.valid }
                                label="Password"
                                type="password"
                                required
                            />
                        </Grid>
                    </Grid>
                    {
                        this.props.error.isError ?
                            <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                                <Grid style={ styles.errorMessage } item>
                                    <div>{ this.props.error.errorMessage }</div>
                                </Grid>
                            </Grid>
                        : null
                    }
                </CardContent>
                <CardActions style={ styles.cardActions }>
                    <Button
                        onClick={ () => this.props.login(this.props.email.value, this.props.password.value) }
                        disabled={ !this.props.email.valid || !this.props.password.valid }
                        color="primary"
                    >
                        Login
                    </Button>
                </CardActions>
                <Button component={ Link } to={ '/create-account' } style={ styles.createAccountBtn }>
                    Create Account
                </Button>
            </Card>
        )
    }
}

const mapStateToProps = state => {
    return {
        error: state.loginForm.error,
        email: state.loginForm.email,
        password: state.loginForm.password
    }
}

function mapDispatchToProps(dispatch) {
    return {
        init: () => { dispatch(init()) },
        emailChange: (e) => { dispatch(emailChange(e.target.value)) },
        passwordChange: (e) => { dispatch(passwordChange(e.target.value)) },
        login: (email, password) => {
            auth().signInWithEmailAndPassword(email, password)
            .then(_ => dispatch(login(null)))
            .catch(error => dispatch(login(error.message)))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LoginFormContainer);
