// React
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth } from 'firebase/app'
// Material
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import Search from '@material-ui/icons/Search';
import FolderOutlined from '@material-ui/icons/FolderOutlined';
import PersonOutline from '@material-ui/icons/PersonOutline';
import PowerSettingsNew from '@material-ui/icons/PowerSettingsNew';
// Actions
import { closeSideNav, toggleSideNav } from '../actions/Drawer.Actions';

const styles = {
    list: {
        width: '250px',
        display: 'block'
    }
};

class DrawerContainer extends Component {
    componentWillMount() {
        this.props.closeSideNav();
    }

    render() {
        return (
            <Drawer open={ this.props.open } onClose={ this.props.toggleSideNav }>
                <List style={ styles.list }>
                    <ListItem component={ Link } to={ '/app/search' } onClick={ this.props.toggleSideNav } button>
                        <ListItemIcon>
                            <Search />
                        </ListItemIcon>
                        <ListItemText primary="Search" />
                    </ListItem>
                    <Divider />
                    <ListItem component={ Link } to={ '/app/folders' } onClick={ this.props.toggleSideNav } button>
                        <ListItemIcon>
                            <FolderOutlined />
                        </ListItemIcon>
                        <ListItemText primary="Folders" />
                    </ListItem>
                    <Divider />
                    <ListItem component={ Link } to={ '/app/account' } onClick={ this.props.toggleSideNav } button>
                        <ListItemIcon>
                            <PersonOutline />
                        </ListItemIcon>
                        <ListItemText primary="Account" />
                    </ListItem>
                    <Divider />
                    <ListItem onClick={ this.props.logout } button>
                        <ListItemIcon>
                            <PowerSettingsNew />
                        </ListItemIcon>
                        <ListItemText primary="Logout" />
                    </ListItem>
                    <Divider />
                </List>
            </Drawer>
        )
    }
}

const mapStateToProps = state => {
    return {
        open: state.sideNav.open
    }
}

function mapDispatchToProps(dispatch) {
    return {
        closeSideNav: () => { dispatch(closeSideNav()) },
        toggleSideNav: () => { dispatch(toggleSideNav()) },
        logout: () => { auth().signOut(); }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DrawerContainer);
