// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth, firestore } from 'firebase/app';
// Material
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Badge from '@material-ui/core/Badge';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Button from '@material-ui/core/Button';
import Folder from '@material-ui/icons/Folder';
// Actions
import { addNewFolder, closeDialog, loadFolders, newFolderNameChange, saveToFolder } from '../actions/Article.Actions';

const styles = {
    list: {
        borderTop: '1px solid #ddd',
        maxHeight: '300px',
        overflow: 'scroll'
    },
    listSubheader: {
        background: 'white',
        zIndex: '2'
    },
    listItem: {
        borderTop: '1px solid #ddd',
        paddingTop: '16px',
        paddingBottom: '8px'
    },
    dialogActions: {
        justifyContent: 'center'
    }
};

class SaveDialogContainer extends Component {
    componentWillMount() {
        this.props.loadFolders();
    }

    render() {
        return (
            <Dialog open={ this.props.dialogOpen }>
                <DialogContent>
                    <Grid alignItems="flex-end" spacing={ 8 } container>
                        <Grid item>
                            <TextField
                                value={ this.props.newFolderName.value }
                                onChange={ this.props.newFolderNameChange }
                                error={ this.props.newFolderName.touched && !this.props.newFolderName.valid }
                                label="Add Folder"
                                required
                            />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions style={ styles.dialogActions }>
                    <Button onClick={ this.props.closeDialog } color="secondary">Cancel</Button>
                    <Button
                        onClick={ () => this.props.addNewFolder(this.props.newFolderName.value) }
                        disabled={ !this.props.newFolderName.valid }
                        color="primary"
                    >
                        Add
                    </Button>
                </DialogActions>
                <List style={ styles.list } subheader={<ListSubheader style={ styles.listSubheader }>Folders</ListSubheader>} component="nav">
                    { this.props.folders.map(folder =>
                        <ListItem
                            key={ folder.id }
                            onClick={ () => this.props.saveToFolder(folder, this.props.isSelectedList) }
                            style={ styles.listItem }
                            button
                        >
                            <ListItemIcon>
                                <Badge badgeContent={ folder.cardsLength || 0 } color="primary">
                                    <Folder />
                                </Badge>
                            </ListItemIcon>
                            <ListItemText primary={ folder.folderName } />
                        </ListItem>
                    ) }
                </List>
            </Dialog>
        )
    }
}

const mapStateToProps = state => {
    return {
        dialogOpen: state.article.dialogOpen,
        newFolderName: state.article.newFolderName,
        folders: state.article.folders,
        isSelectedList: state.article.isSelectedList
    }
}

function mapDispatchToProps(dispatch) {
    return {
        addNewFolder: (folderName) => {
            const docRef = firestore().collection('folders').doc();
            firestore().collection('folders').doc(docRef.id).set({
                cardsLength: 0,
                folderName: folderName.trim(),
                id: docRef.id,
                userUid: auth().currentUser.uid
            })
            .then(_ => {
                dispatch(addNewFolder())
            });
        },
        closeDialog: () => { dispatch(closeDialog()); },
        loadFolders: () => {
            auth().onAuthStateChanged(userData => {
                if (userData) {
                    firestore().collection('folders').where('userUid', '==', userData.uid).onSnapshot(foldersData => {
                        const folders = foldersData.docs.map(folder => folder.data()).sort((a, b) => {
                            if (a.folderName.toLowerCase() < b.folderName.toLowerCase())
                                return -1;
                            if (a.folderName.toLowerCase() > b.folderName.toLowerCase())
                                return 1;
                            return 0;
                        });
                        dispatch(loadFolders(folders));
                    });
                } else {
                    dispatch(loadFolders([]));
                }
            });
        },
        newFolderNameChange: (e) => { dispatch(newFolderNameChange(e.target.value)); },
        saveToFolder: (folder, isSelectedList) => {
            const batch = firestore().batch();

            const newFolder = {
                cardsLength: folder.cardsLength + isSelectedList.length,
                folderName: folder.folderName,
                id: folder.id,
                userUid: folder.userUid,
            };
    
            isSelectedList.forEach(selected => {
                selected.isSaved = true;
                const cardRef = firestore().collection('cards').doc();
                batch.set(cardRef, {
                    folder: newFolder,
                    id: cardRef.id,
                    header: selected.header || null,
                    paragraphIndex: selected.paragraphIndex,
                    sectionIndex: selected.sectionIndex,
                    text: selected.text
                })
            });
    
            const folderRef = firestore().collection('folders').doc(newFolder.id);
            folderRef.update({ cardsLength: newFolder.cardsLength });
    
            batch.commit()
            .then(_ => {
                const isSelectedListIds = isSelectedList.map(paragraph => paragraph.paragraphId);
                dispatch(saveToFolder(isSelectedListIds));
            });
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SaveDialogContainer);
