// React
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Material
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import ArrowBack from '@material-ui/icons/ArrowBack';
import Save from '@material-ui/icons/Save';
// Actions
import { openDialog } from '../actions/Article.Actions';

const styles = {
    appBar: {
        display: 'flex',
        justifyContent: 'space-between',
        paddingLeft: '8px',
        paddingRight: '8px',
        alignItems: 'center',
        position: 'sticky',
        top: '0px',
        zIndex: '999',
        flexDirection: 'row',
    },
    title: {
        opacity: '.6',
        textTransform: 'capitalize'
    }
};

class ToolBarContainer extends Component {
    render() {
        return (
            <AppBar style={ styles.appBar } position="relative" color="default">
                <IconButton component={ Link } to={ '/app/search' } color="primary">
                    <ArrowBack />
                </IconButton>
                <h3 style={ styles.title }>{ this.props.title }</h3>
                <IconButton onClick={ this.props.openDialog } disabled={ !this.props.isSelectedList.length } style={ styles.saveIcon } color="primary">
                    <Save />
                </IconButton>
            </AppBar>
        )
    }
}

const mapStateToProps = state => {
    return {
        title: state.article.title,
        isSelectedList: state.article.isSelectedList
    }
}

function mapDispatchToProps(dispatch) {
    return {
        openDialog: () => { dispatch(openDialog()); }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ToolBarContainer)
