// Action Types
import { ActionTypes } from '../actions/Article.Actions';

const init = {
    title: '',
    sections: [],
    isSelectedList: [],
    dialogOpen: false,
    folders: [],
    newFolderName: {
        touched: false,
        valid: true,
        value: ''
    }
};

const article = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.CLEAR_ARTICLE:
        return Object.assign({}, state, {
            title: '',
            sections: [],
            isSelectedList: [],
            dialogOpen: state.dialogOpen,
            folders: state.folders,
            newFolderName: {
                touched: false,
                valid: true,
                value: ''
            }
        });

        case ActionTypes.LOAD_ARTICLE:
        return Object.assign({}, state, { title: action.payload.title, sections: action.payload.sections });

        case ActionTypes.SELECT_SECTION:
        return Object.assign({}, state, {
            sections: [...state.sections],
            isSelectedList:
                action.payload.isSelected ?
                [action.payload, ...state.isSelectedList] :
                state.isSelectedList.filter(paragraph => paragraph.paragraphId !== action.payload.paragraphId),
        });

        case ActionTypes.OPEN_DIALOG:
        return Object.assign({}, state, { dialogOpen: true });

        case ActionTypes.CLOSE_DIALOG:
        return Object.assign({}, state, {
            dialogOpen: false,
            newFolderName: {
                touched: false,
                valid: true,
                value: ''
            }
        });

        case ActionTypes.LOAD_FOLDERS:
        return Object.assign({}, state, { folders: action.payload });

        case ActionTypes.NEW_FOLDER_NAME_CHANGE:
        return Object.assign({}, state, {
            newFolderName: {
                touched: true,
                valid: action.payload.length,
                value: action.payload
            }
        });

        case ActionTypes.ADD_NEW_FOLDER:
        return Object.assign({}, state, {
            newFolderName: {
                touched: false,
                valid: true,
                value: ''
            }
        });

        case ActionTypes.SAVE_TO_FOLDER:
        return Object.assign({}, state, {
            sections: state.sections.map(section =>
                Object.assign({}, section, {
                    paragraphs: section.paragraphs.filter(paragraph => !action.payload.includes(paragraph.paragraphId))
                })
            ),
            isSelectedList: [],
            dialogOpen: false,
            newFolderName: {
                touched: false,
                valid: true,
                value: ''
            }
        });

        default:
        return state
    }
}

export default article;
