const directory = 'ARTICLE_';

export const ActionTypes = {
    CLEAR_ARTICLE: `${ directory }CLEAR_ARTICLE`,
    LOAD_ARTICLE: `${ directory }LOAD_ARTICLE`,
    SELECT_SECTION: `${ directory }SELECT_SECTION`,
    SAVE_TO_FOLDER: `${ directory }SAVE_TO_FOLDER`,
    OPEN_DIALOG: `${ directory }OPEN_DIALOG`,
    CLOSE_DIALOG: `${ directory }CLOSE_DIALOG`,
    LOAD_FOLDERS: `${ directory }LOAD_FOLDERS`,
    NEW_FOLDER_NAME_CHANGE: `${ directory }NEW_FOLDER_NAME_CHANGE`,
    ADD_NEW_FOLDER: `${ directory }ADD_NEW_FOLDER`
};

export function clearArticle() {
    return { type: ActionTypes.CLEAR_ARTICLE }
}

export function loadArticle({ title, sections }) {
    return { type: ActionTypes.LOAD_ARTICLE, payload: { title, sections } }
}

export function selectSection(paragraph) {
    return { type: ActionTypes.SELECT_SECTION, payload: paragraph }
}

export function saveToFolder(isSelectedListIds) {
    return { type: ActionTypes.SAVE_TO_FOLDER, payload: isSelectedListIds }
}

export function openDialog() {
    return { type: ActionTypes.OPEN_DIALOG }
}

export function closeDialog() {
    return { type: ActionTypes.CLOSE_DIALOG }
}

export function loadFolders(folders) {
    return { type: ActionTypes.LOAD_FOLDERS, payload: folders }
}

export function newFolderNameChange(newFolderName) {
    return { type: ActionTypes.NEW_FOLDER_NAME_CHANGE, payload: newFolderName }
}

export function addNewFolder() {
    return { type: ActionTypes.ADD_NEW_FOLDER }
}
