// React
import React from 'react';
// Redux
import { connect } from 'react-redux'
// Material
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import LockOpen from '@material-ui/icons/LockOpen';
// Actions
import { openResetPasswordDialog } from '../actions/ResetPassword.Action';

const styles = {
    appBar: {
        height: '64px',
        flexDirection: 'row',
        alignItems: 'center',
        paddingLeft: '16px',
        justifyContent: 'space-between'
    },
    lockOpenIcon: {
        color: 'white'
    }
};

const ToolBarContainer = ({ dispatch }) => (
    <AppBar style={ styles.appBar } position="fixed" color="primary">
        Click Wik
        <IconButton
            onClick={ () => dispatch(openResetPasswordDialog()) }
            style={ styles.lockOpenIcon }
        >
            <LockOpen />
        </IconButton>
    </AppBar>
)

export default connect()(ToolBarContainer)
