// Action Types
import { ActionTypes } from '../actions/Account.Actions';

const init = {
    email: {
        touched: false,
        valid: false,
        value: ''
    },
    password: {
        touched: false,
        valid: false,
        value: ''
    },
    error: {
        isError: false,
        errorMessage: ''
    },
    deleteAccountDialogOpen: false
};

const emailPattern = new RegExp(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/);

const account = (state = init, action) => {
    switch (action.type) {

        case ActionTypes.RESET_FORM:
        return Object.assign({}, {
            email: {
                touched: false,
                valid: false,
                value: ''
            },
            password: {
                touched: false,
                valid: false,
                value: ''
            },
            error: {
                isError: false,
                errorMessage: ''
            },
            deleteAccountDialogOpen: false
        });

        case ActionTypes.EMAIL_CHANGE:
        return Object.assign({}, state, {
            email: {
                touched: true,
                valid: emailPattern.test(action.payload) && action.payload.length,
                value: action.payload
            },
            error: {
                isError: false,
                errorMessage: ''
            }
        });

        case ActionTypes.PASSWORD_CHANGE:
        return Object.assign({}, state, {
            password: {
                touched: true,
                valid: action.payload.length,
                value: action.payload
            },
            error: {
                isError: false,
                errorMessage: ''
            },
        });

        case ActionTypes.SAVE_EMAIL:
        return Object.assign({}, state, {
            email: action.payload.isError ? {
                touched: true,
                valid: true,
                value: state.email.value
            } : {
                touched: false,
                valid: false,
                value: ''
            },
            error: action.payload
        });

        case ActionTypes.SAVE_PASSWORD:
        return Object.assign({}, state, {
            password: action.payload.isError ? {
                touched: true,
                valid: true,
                value: state.password.value
            } : {
                touched: false,
                valid: false,
                value: ''                
            },
            error: action.payload
        });

        case ActionTypes.OPEN_DELETE_ACCOUNT_DIALOG:
        return Object.assign({}, state, { deleteAccountDialogOpen: true });

        case ActionTypes.CLOSE_DELETE_ACCOUNT_DIALOG:
        return Object.assign({}, state, { deleteAccountDialogOpen: false });

        default:
        return state
    }
}

export default account;