// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth, firestore } from 'firebase/app';
// Router
import { Link } from 'react-router-dom';
// Material
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Email from '@material-ui/icons/Email';
import VpnKey from '@material-ui/icons/VpnKey';
// Actions
import { emailChange, init, passwordChange, createAccount } from '../actions/CreateAccount.Actions';

const styles = {
    card: {
        width: '300px',
        height: 'auto',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-evenly',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px'
    },
    cardHeader: {
        borderBottom: '1px solid #ddd'
    },
    cardContent: {
        height: '100%',
        display: 'flex',
        justifyContent: 'space-evenly',
        flexDirection: 'column'
    },
    gridContainer: {
        justifyContent: 'space-evenly',
        marginTop: '8px'
    },
    cardActions: {
        flexDirection: 'row-reverse',
        paddingBottom: '24px',
    },
    createAccountBtn: {
        borderTop: '1px solid #ddd',
        height: '64px',
        width: '100%'
    },
    errorMessage: {
        color: 'red',
        border: '1px solid red',
        fontSize: '12px',
        margin: '24px 16px 0px',
        textAlign: 'center'
    }
};

class CreateAccountFormContainer extends Component {
    componentWillMount() {
        this.props.init();
    }

    render() {
        return (
            <Card style={ styles.card }>
                <CardHeader title="Create Account" style={ styles.cardHeader }></CardHeader>
                <CardContent style={ styles.cardContent }>
                    <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                        <Grid item><Email color="action" /></Grid>
                        <Grid item>
                            <TextField
                                onChange={ this.props.emailChange }
                                value={ this.props.email.value }
                                error={ this.props.email.touched && !this.props.email.valid }
                                label="Email"
                                type="email"
                                required
                            />
                        </Grid>
                    </Grid>
                    <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                        <Grid item><VpnKey color="action" /></Grid>
                        <Grid item>
                            <TextField
                                onChange={ this.props.passwordChange }
                                value={ this.props.password.value }
                                error={ this.props.password.touched && !this.props.password.valid }
                                label="Password"
                                type="password"
                                required
                            />
                        </Grid>
                    </Grid>
                    {
                        this.props.error.isError ?
                            <Grid alignItems="flex-end" spacing={ 16 } style={ styles.gridContainer } container>
                                <Grid style={ styles.errorMessage } item>
                                    <div>{ this.props.error.errorMessage }</div>
                                </Grid>
                            </Grid>
                        : null
                    }
                </CardContent>
                <CardActions style={ styles.cardActions }>
                    <Button
                        onClick={ () => this.props.createAccount(this.props.email.value, this.props.password.value) }
                        disabled={ !this.props.email.valid || !this.props.password.valid }
                        color="primary"
                    >
                        Create Account
                    </Button>
                </CardActions>
                <Button component={ Link } to={ '/login' } style={ styles.createAccountBtn }>
                    Login
                </Button>
            </Card>
        )
    }
}

const mapStateToProps = state => {
    return {
        error: state.createAccount.error,
        email: state.createAccount.email,
        password: state.createAccount.password
    }
}

function mapDispatchToProps(dispatch) {
    return {
        init: () => { dispatch(init()) },
        emailChange: (e) => { dispatch(emailChange(e.target.value)) },
        passwordChange: (e) => { dispatch(passwordChange(e.target.value)) },
        createAccount: (email, password) => {
            auth().createUserWithEmailAndPassword(email, password)
            .then(userData => {
                const createUser = firestore().collection('users').doc(userData.user.uid).set({
                    email: userData.user.email,
                    language: 'en',
                    uid: userData.user.uid
                })
                const signInUser = auth().signInWithEmailAndPassword(email, password);

                Promise.all([createUser, signInUser])
                .then(_ => dispatch(createAccount(null)))
                .catch(error => dispatch(createAccount(error.message)));
            })
            .catch(error => dispatch(createAccount(error.message)));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateAccountFormContainer);
