const language = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_LANGUAGE:
        return Object.assign({}, state, { languageSelected: action.payload });

        case ActionTypes.SELECT_LANGUAGE:
        return Object.assign({}, state, { languageSelected: action.payload });

        default:
        return state
    }
}

export default language;