// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Axion
import axios from 'axios';
// Material
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
// Actions
import { clearArticle, loadArticle, selectSection } from '../actions/Article.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';

const styles = {
    card: {
        marginBottom: '24px'
    },
    sectionHeader: {
        textAlign: 'center',
        margin: '0px 24px 24px',
        paddingTop: '48px',
        paddingBottom: '8px',
        borderBottom: '1px solid #ddd',
        fontSize: '24px'
    },
    cardContent: {
        borderTop: '1px solid #ddd',
        borderBottom: '1px solid #ddd'
    },
    isSaved: {
        display: 'none',
    },
    isSelectedContent: {
        background: '#ccc',
        borderBottomColor: '#aaa',
    },
    isSelectedActions: {
        background: '#ddd'
    }
};

class ArticleContainer extends Component {
    componentWillMount() {
        this.props.loadArticle();
        this.props.updateTitle();
    }
    componentWillUnmount() {
        this.props.clearArticle();
    }
    render() {
        return (
            this.props.sections.map((section, sIndex) =>
                <div key={ sIndex }>{
                    (section.paragraphs && section.paragraphs.length) ?
                        <div>
                            {
                                (section.header && section.paragraphs.length) ?
                                <h4 style={ styles.sectionHeader }>{ section.header }</h4> :
                                null 
                            }
                            { section.paragraphs.map((paragraph, pIndex) =>
                                <Card key={ pIndex } style={ paragraph.isSaved ? styles.isSaved : styles.card }>
                                    <CardContent style={ paragraph.isSelected ? styles.isSelectedContent : styles.cardContent }>
                                        { paragraph.text }
                                    </CardContent> 
                                    <CardActions
                                        style={ paragraph.isSelected ? styles.isSelectedActions : null }
                                    >
                                        <Button onClick={ () => this.props.selectSection(paragraph) } color="primary" fullWidth>
                                            { paragraph.isSelected ? 'Remove' : 'Select' }
                                        </Button>
                                    </CardActions>
                                </Card>
                            ) }
                        </div>
                    : null
                }</div>
            )
        )
    }
}

const mapStateToProps = state => {
    return {
        sections: state.article.sections
    }
}

function mapDispatchToProps(dispatch) {
    return {
        loadArticle: async () => {
            const title = window.location.hash.slice(17);
            const response = await axios.get(`https://${ window.location.hash.slice(14, 16) }.wikipedia.org/w/api.php?origin=*&format=json&action=query&prop=extracts|images&redirects=true&titles=${ title }`);
            const responseHtml = response.data.query.pages[Object.keys(response.data.query.pages)[0]].extract;
            const sections = responseHtml.match(/<h(.)>.*?<\/h\1>|<p>.*?<\/p>/g)
            .reduce((prev, curr, i) => {
                const tmp = document.createElement('DIV');
                tmp.innerHTML = curr;
                const text = tmp.textContent || tmp.innerText || '';
                if (curr.match(/<h(.)>.*?<\/h\1>/g) && text) {
                    prev.push({
                        header: text,
                        paragraphs: [],
                        sectionIndex: prev.length
                    })
                } else if (curr.match(/<p>.*?<\/p>/g) && text) {
                    prev[prev.length - 1].paragraphs.push({
                        header: prev[prev.length - 1].header,
                        isSelected: false,
                        isSaved: false,
                        paragraphId: i,
                        paragraphIndex: prev[prev.length - 1].paragraphs.length,
                        sectionIndex: prev[prev.length - 1].sectionIndex,
                        text: text
                    });
                }
                return prev;
            }, [{ header: '', paragraphs: [], sectionIndex: 0 }]);
            dispatch(loadArticle({
                title: title.replace('%20', ' '),
                sections: sections
            }));
        },
        updateTitle: () => { dispatch(updateTitle('Article')); },
        clearArticle: () => { dispatch(clearArticle()); },
        selectSection: (paragraph) => {
            paragraph.isSelected = !paragraph.isSelected;
            dispatch(selectSection(paragraph));
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ArticleContainer);
