// React
import React from 'react';
import { Link } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Material
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import ArrowBack from '@material-ui/icons/ArrowBack';

const styles = {
    appBar: {
        display: 'flex',
        justifyContent: 'space-between',
        paddingLeft: '8px',
        paddingRight: '8px',
        alignItems: 'center',
        position: 'sticky',
        top: '0px',
        zIndex: '999',
        flexDirection: 'row'
    },
    folderName: {
        width: '100%',
        textAlign: 'center',
        marginRight: '48px',
        opacity: '.6',
        textTransform: 'capitalize'
    }
};

const ToolBarContainer = ({ folderName }) => (
    <AppBar style={ styles.appBar } position="relative" color="default">
        <IconButton component={ Link } to={ '/app/folders' } color="primary">
            <ArrowBack />
        </IconButton>
        <h3 style={ styles.folderName }>
            { folderName || '' }
        </h3>
    </AppBar>
);

const mapStateToProps = state => {
    return {
        folderName: state.cards.folderName
    }
}

export default connect(mapStateToProps)(ToolBarContainer)
