// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { firestore } from 'firebase/app';
// Material
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
// Actions
import { closeDeleteCardDialog, confirmDeleteCard } from '../actions/Cards.Actions';

class DeleteCardContainer extends Component {
    render() {
        return (
            <Dialog open={ this.props.deleteCardDialogOpen }>
                <DialogTitle>Confirm Delete</DialogTitle>
                <DialogContent>
                    <Grid alignItems="flex-end" container>
                        <Grid item>Are you sure you want to delete this card?</Grid>
                    </Grid>   
                </DialogContent>
                <DialogActions>
                    <Button onClick={ this.props.closeDeleteCardDialog } color="secondary">Cancel</Button>
                    <Button
                        onClick={ () => this.props.confirmDeleteCard(this.props.deleteCardRef, this.props.activeIndex, this.props.cards) }
                        color="primary"
                    >
                        Confirm
                    </Button>
                </DialogActions>
            </Dialog>
        )
    }
};

const mapStateToProps = state => {
    return {
        deleteCardDialogOpen: state.cards.deleteCardDialogOpen,
        deleteCardRef: state.cards.deleteCardRef,
        activeIndex: state.cards.activeIndex,
        cards: state.cards.cards
    }
}

function mapDispatchToProps(dispatch) {
    return {
        closeDeleteCardDialog: () => { dispatch(closeDeleteCardDialog()) },
        confirmDeleteCard: (deleteCardRef, activeIndex, cards) => {
            const batch = firestore().batch();
            const folder = {
                cardsLength: deleteCardRef.folder.cardsLength - 1,
                folderName: deleteCardRef.folder.folderName,
                id: deleteCardRef.folder.id,
                userUid: deleteCardRef.folder.userUid,
            };
    
            const deletedCardRef = firestore().collection('cards').doc(deleteCardRef.id);
            batch.delete(deletedCardRef);
    
            const folderRef = firestore().collection('folders').doc(deleteCardRef.folder.id);
            batch.update(folderRef, { cardsLength: folder.cardsLength });
    
            cards.forEach(card => {
                if (card.id !== deleteCardRef.id) {
                    const cardRef = firestore().collection('cards').doc(card.id);
                    batch.update(cardRef, { folder: folder });
                }
            });
    
            batch.commit()
            .then(_ => {
                dispatch(confirmDeleteCard((activeIndex === (cards.length - 1)) ? (cards.length - 2) : activeIndex))
                dispatch({
                    type: 'CONFIRM_DELETE_CARD',
                    payload: (activeIndex === (cards.length - 1)) ? (cards.length - 2) : activeIndex
                })
            });
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DeleteCardContainer)
