// React
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Material
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListAlt from '@material-ui/icons/ListAlt';

const styles = {
    listItem: {
        borderBottom: '1px solid #ddd'
    },
    listItemText: {
        textTransform: 'capitalize'
    }
};

class ArticlesListContainer extends Component {
    render() {
        return (
            <List component="nav">{
                this.props.articles.map((article, i) =>
                    <ListItem
                        key={ i }
                        component={ Link } to={ `/app/article/${ this.props.language }/${ article }` }
                        style={ styles.listItem }
                        button
                    >
                        <ListItemIcon><ListAlt /></ListItemIcon>
                        <ListItemText style={ styles.listItemText } primary={ article } />
                    </ListItem>
                )
            }</List>
        )
    }
}

const mapStateToProps = state => {
    return {
        articles: state.searchForm.articles,
        language: state.searchForm.language
    }
}

export default connect(mapStateToProps)(ArticlesListContainer)
