// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Material
import AppBar from '@material-ui/core/AppBar';
import IconButton from '@material-ui/core/IconButton';
import Menu from '@material-ui/icons/Menu';
// Actions
import { toggleSideNav } from '../actions/Drawer.Actions';

const styles = {
    menuIcon: {
        color: 'white'
    },
    appBar: {
        height: '64px',
        flexDirection: 'row',
        display: 'flex',
        paddingLeft: '8px',
        paddingRight: '8px',
        alignItems: 'center',
        zIndex: '999',
    }
};

class ToolBarContainer extends Component {
    render() {
        return (
            <AppBar style={ styles.appBar } position="fixed" color="primary">
                <IconButton onClick={ this.props.toggleSideNav } style={ styles.menuIcon }>
                    <Menu />
                </IconButton>
                { this.props.title }
            </AppBar>
        )
    }
};

const mapStateToProps = state => {
    return {
        title: state.sideNav.title
    }
}

function mapDispatchToProps(dispatch) {
    return {
        toggleSideNav: () => { dispatch(toggleSideNav()) }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ToolBarContainer);
