// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Firebase
import { firestore } from 'firebase/app';
// Material
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import ArrowBack from '@material-ui/icons/ArrowBack';
import ArrowForward from '@material-ui/icons/ArrowForward';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
// Actions
import { loadCards, clearCards, deleteCard, previous, next } from '../actions/Cards.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';

const styles = {
    card: {
        position: 'absolute',
        top: '72px',
        left: '24px',
        right: '24px',
        bottom: '24px'
    },
    cardContent: {
        borderTop: '1px solid #ddd',
        borderBottom: '1px solid #ddd',
        overflow: 'auto',
        height: '100%'
    },
    cardActions: {
        background: 'white',
        justifyContent: 'space-around',
        position: 'absolute',
        width: '100%',
        bottom: '0px',
        borderTop: '1px solid #ddd'
    },
    deleteBtn: {
        color: '#E57373'
    },
    button: {
        width: '50%'
    },
    active: {
        display: 'block'
    },
    inActive: {
        display: 'none'
    }
};

class CardsContainer extends Component {
    componentWillMount() {
        this.props.loadCards();
        this.props.updateTitle();
    }

    componentWillUnmount() {
        this.props.clearCards();
    }

    render() {
        return (
            this.props.cards.map((card, i) =>
                this.props.activeIndex === i ?
                    <Card key={ card.id } style={ styles.card }>
                        <CardHeader
                            title={ card.header }
                            action={
                                <IconButton onClick={ () => this.props.deleteCard(card) }>
                                    <DeleteOutline style={ styles.deleteBtn } />
                                </IconButton>
                            }
                        ></CardHeader>
                        <CardContent style={ styles.cardContent }>
                            { card.text }
                        </CardContent> 
                        <CardActions style={ styles.cardActions }>
                            <Button
                                onClick={ this.props.previous }
                                style={ styles.button }
                                disabled={ i === 0 }
                                color="primary"
                            >
                                <ArrowBack />
                            </Button>
                            <Button
                                onClick={ this.props.next }
                                style={ styles.button }
                                disabled={ i === this.props.cards.length - 1 }
                                color="primary"
                            >
                                <ArrowForward />
                            </Button>
                        </CardActions>
                    </Card>
                : null
            )
        )
    }
}

const mapStateToProps = state => {
    return {
        cards: state.cards.cards,
        activeIndex: state.cards.activeIndex
    }
}

function mapDispatchToProps(dispatch) {
    return {
        updateTitle: () => { dispatch(updateTitle('Cards')) },
        loadCards: () => {
            firestore().collection('cards').where('folder.id', '==', window.location.hash.slice(12)).onSnapshot(cardsData => {
                if (!cardsData.empty) {
                    const folderName = cardsData.docs[0].data().folder.folderName;
                    const cards = cardsData.docs.map(card => card.data()).sort((a, b) => {
                        if (a.sectionIndex === b.sectionIndex) {
                            return b.paragraphIndex - a.paragraphIndex;
                        }
                        return a.sectionIndex > b.sectionIndex ? 1 : -1;
                    });
                    dispatch(loadCards({ cards: cards, folderName: folderName }))
                } else {
                    dispatch(loadCards({ cards: [], folderName: 'No Cards In Folder' }))
                }
            });
        },
        clearCards: () => { dispatch(clearCards()) },
        deleteCard: (card) => { dispatch(deleteCard(card)) },
        previous: () => { dispatch(previous()) },
        next: () => { dispatch(next()) }
    }
}

export default connect(mapStateToProps, mapDispatchToProps,)(CardsContainer);
