const directory = 'CREATE_ACCOUNT_';

export const ActionTypes = {
    INIT: `${ directory }INIT`,
    EMAIL_CHANGE: `${ directory }EMAIL_CHANGE`,
    PASSWORD_CHANGE: `${ directory }PASSWORD_CHANGE`,
    CREATE_ACCOUNT: `${ directory }CREATE_ACCOUNT`,
};

export function init() {
    return { type: ActionTypes.INIT }
}

export function emailChange(email) {
    return { type: ActionTypes.EMAIL_CHANGE, payload: email }
}

export function passwordChange(password) {
    return { type: ActionTypes.PASSWORD_CHANGE, payload: password }
}

export function createAccount(error) {
    return { type: ActionTypes.CREATE_ACCOUNT, payload: error }
}
