// React
import React from 'react';
import { Route, Switch } from 'react-router-dom';
// Containers
import SearchLayout from '../../search/components/SearchLayout.Component';
import LanguageLayout from '../../language/components/LanguageLayout.Component';
import ArticleLayout from '../../article/components/ArticleLayout.Component';
import FoldersLayout from '../../folders/components/FoldersLayout.Component';
import CardsLayout from '../../cards/components/CardsLayout.Component';
import AccountLayout from '../../account/components/AccountLayout.Component';
// Components
import ToolBarContainer from '../containers/ToolBar.Container';
import DrawerContainer from '../containers/Drawer.Container';

const styles = {
    mainContainer: {
    },
    innerContainer: {
        top: '64px',
        bottom: '0px',
        position: 'absolute',
        width: '100%',
        overflow: 'auto'
    }
}

function SideNavLayout() {
    return(
        <div style={ styles.mainContainer }>
            <DrawerContainer />

            <ToolBarContainer />

            <div style={ styles.innerContainer }>
                <Switch>
                    <Route path='/app/search' component={ SearchLayout } />
                    <Route path='/app/language' component={ LanguageLayout } />
                    <Route path='/app/article/:language/:title' component={ ArticleLayout } />
                    <Route path='/app/folders' component={ FoldersLayout } />
                    <Route path='/app/cards' component={ CardsLayout } />
                    <Route path='/app/account' component={ AccountLayout } />
                </Switch>
            </div>
        </div>
    )
}

export default SideNavLayout;