const directory = 'SEARCH_FORM_';

export const ActionTypes = {
    INIT: `${ directory }INIT`,
    LOAD_LANGUAGE: `${ directory }LOAD_LANGUAGE`,
    SEARCH_TITLE: `${ directory }SEARCH_TITLE`
};

export function init() {
    return { type: ActionTypes.INIT }
}

export function loadLanguage(language) {
    return { type: ActionTypes.LOAD_LANGUAGE, payload: language }
}

export function searchTitle(response) {
    return { type: ActionTypes.SEARCH_TITLE, payload: response }
}
