// Action Types
import { ActionTypes } from '../actions/Cards.Actions';

const init = {
    activeIndex: 0,
    cards: [],
    deleteCardDialogOpen: false,
    deleteCardRef: {},
    folderName: ''
};

const cards = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.LOAD_CARDS:
        return Object.assign({}, state, {
            cards: action.payload.cards,
            folderName: action.payload.folderName
        });

        case ActionTypes.CLEAR_CARDS:
        return Object.assign({}, {
            cards: [],
            activeIndex: 0,
            deleteCardDialogOpen: false,
            deleteCardRef: {},
            folderName: ''
        });

        case ActionTypes.PREVIOUS:
        return Object.assign({}, state, { activeIndex: state.activeIndex - 1 });

        case ActionTypes.NEXT:
        return Object.assign({}, state, { activeIndex: state.activeIndex + 1 });

        case ActionTypes.DELETE_CARD:
        return Object.assign({}, state, { deleteCardDialogOpen: true, deleteCardRef: action.payload });

        case ActionTypes.CONFIRM_DELETE_CARD:
        return Object.assign({}, state, { activeIndex: action.payload, deleteCardDialogOpen: false, deleteCardRef: {} });

        case ActionTypes.CLOSE_DELETE_CARD_DIALOG:
        return Object.assign({}, state, { deleteCardDialogOpen: false, deleteCardRef: {} });

        default:
        return state
    }
}

export default cards;