// React
import React, { Component } from 'react';
// Redux
import { connect } from 'react-redux'
// Router
import { Link } from 'react-router-dom';
// Axion
import axios from 'axios';
// Firebase
import { auth, firestore } from 'firebase/app';
// Material
import AppBar from '@material-ui/core/AppBar';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import Language from '@material-ui/icons/Language';
// Actions
import { init, loadLanguage, searchTitle } from '../actions/SearchForm.Actions';
import { updateTitle } from '../../sideNav/actions/Drawer.Actions';

const styles = {
    appBar: {
        zIndex: '1'
    },
    textField: {
        width: '100%'
    },
    gridContainer: {
        padding: '8px 8px 8px'
    },
    gridTextField: {
        flex: '1'
    }
};

class SearchFormContainer extends Component {
    componentWillMount() {
        this.props.init();
        this.props.loadLanguage();
        this.props.updateTitle();
    }

    render() {
        return (
            <AppBar style={ styles.appBar } position="relative" color="default">
                <Grid spacing={ 16 } style={ styles.gridContainer } alignItems="flex-end" container>
                    <Grid item>
                        <IconButton component={ Link } to={ '/app/language' } color="primary">
                            <Language />
                        </IconButton>
                    </Grid>
                    <Grid style={ styles.gridTextField } item>
                        <TextField
                            onChange={ (e) => this.props.searchTitle(e.target.value, this.props.language) }
                            style={ styles.textField }
                            label="Search Articles"
                        />
                    </Grid>
                </Grid>
            </AppBar>
        )
    }
}

const mapStateToProps = state => {
    return {
        language: state.searchForm.language
    }
}

function mapDispatchToProps(dispatch) {
    return {
        updateTitle: () => { dispatch(updateTitle('Search')) },
        init: () => { dispatch(init()) },
        loadLanguage: () => {
            auth().onAuthStateChanged(userData => {
                if (userData) {
                    firestore().collection('users').doc(userData.uid).get()
                    .then(user => dispatch(loadLanguage((user.data() && user.data().language) ? user.data().language : 'en')));
                }
            });
        },
        searchTitle: async (title, language) => {
            const response = title ? await axios.get('https://' + language + '.wikipedia.org/w/api.php?action=opensearch&format=json&origin=*&search=' + title) : null
            dispatch(searchTitle(response ? response.data[1] : []))
        },
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchFormContainer);
