// Action Types
import { ActionTypes } from '../actions/App.Actions';

const init = {
    isLoggedIn: false,
    hasLoaded: false
};

const account = (state = init, action) => {
    switch (action.type) {
        case ActionTypes.CHECK_USER:
        return Object.assign({}, {
            isLoggedIn: action.payload,
            hasLoaded: true
        });

        default:
        return state
    }
}

export default account;