// React
import React, { Component } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
// Redux
import { connect } from 'react-redux'
// Firebase
import { auth } from 'firebase/app';
// Components
import LoginLayout from '../../login/components/LoginLayout.Component';
import CreateAccountLayout from '../../create-account/components/CreateAccountLayout.Component';
import SideNavLayout from '../../sideNav/components/SideNavLayout.Component';
// Actions
import { checkUser } from '../actions/App.Actions';

const LoginRoute = ({ component: Component, ...rest }) => {
    return rest.isLoggedIn ?
        <Redirect to='/app/search' /> :
        <Route exact path='/login' component={ LoginLayout } />;
}

const CreateAccountRoute = ({ component: Component, ...rest }) => {
    return rest.isLoggedIn ?
        <Redirect to='/app/search' /> :
        <Route exact path='/create-account' component={ CreateAccountLayout } />;
}

const AppRoute = ({ component: Component, ...rest }) => {
    return !rest.isLoggedIn ?
        <Redirect to='/login' /> :
        <Route path='/app' component={ SideNavLayout } />;
}

class AppContainer extends Component {
    componentDidMount() {
        this.props.checkUser();
    }

    render() {
        return (
            this.props.hasLoaded ?
            <Switch>
                <LoginRoute path='/login' { ...this.props } />
                <CreateAccountRoute path='/create-account' { ...this.props } />
                <AppRoute path='/app' { ...this.props } />
                <Redirect from="/" to="/login" />
            </Switch>
            : null
        )
    }
}

const mapStateToProps = state => {
    return {
        isLoggedIn: state.app.isLoggedIn,
        hasLoaded: state.app.hasLoaded
    }
}

function mapDispatchToProps(dispatch) {
    return {
        checkUser: () => {
            auth().onAuthStateChanged(userData => dispatch(checkUser(userData ? true : false)));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppContainer);
